@import url("https://fonts.googleapis.com/css2?family=Lexend:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Hahmlet:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=League+Spartan:wght@500&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-inter: Inter;
  --font-lexend: Lexend;
  --font-hahmlet: Hahmlet;
  --font-outfit: Outfit;
  --font-roboto: Roboto;
  --font-league-spartan: "League Spartan";

  /* font sizes */
  --font-size-sm: 14px;
  --font-size-29xl: 48px;
  --font-size-lg: 18px;
  --font-size-xs: 12px;
  --font-size-17xl: 36px;
  --font-size-5xl: 24px;
  --font-size-xl: 20px;
  --font-size-13xl: 32px;

  /* Colors */
  --color-white: #fff;
  --color-black: #000;
  --color-gray-100: #201e1e;
  --color-gray-200: rgba(255, 255, 255, 0);
  --color-gainsboro: rgba(217, 217, 217, 0);
  --color-palegreen: #c0ff9a;
  --color-lawngreen-100: #69e11f;

  /* border radiuses */
  --br-3xs: 10px;
  --br-13xl: 32px;
  --br-xs: 12px;
  --br-sm: 14px;
  --br-xl: 20px;
}
